.home_our_services{
    width: 100%;
    max-width: 1000px;
    padding: 3rem 1rem;
    margin: 1rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    border-radius: 30px 0 30px 0;
    /* border: 2px solid var(--green); */

    /* background-color: rgba(24, 156, 1, 0.144); */
    /* background-color: rgb(216, 239, 212); */
    background-color: #e0f0e8;
}
.home_our_services > h2{
    font-size: 2rem;
    font-weight: bold;
    color: #1a202c;
    font-family: Roboto, Arial, sans-serif;
    margin-right:3rem;
}
.home_our_services > ul{
    max-width: 500px;
}
.home_our_services > ul > li{
    font-size: 1.2rem;
    padding: 2.5px 0;
    color: #1a202c;
    font-family: Roboto, Arial, sans-serif;
    cursor: pointer;
    transition-duration: 0.3s;
    transform-origin: center left;
}
.home_our_services > ul > li:hover{
    transform: scale(1.1);
    text-decoration:underline;
}
@media screen and (max-width: 760px){
    .home_our_services{
        flex-direction: column;
    }
    .home_our_services > ul > li,
    .home_our_services > h2{
     width: 100%;
      text-align: center;
      padding: 8px 0;
    }
    .home_our_services > ul > li{
        font-size: 1.1rem;
    }
    .home_our_services > h2{
        margin-right: unset;
    }
}